import React, { useState, useEffect } from "react"
import { PageProps, Link, graphql } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"

import {
	ComposableMap,
	ZoomableGroup,
	Geographies,
	Geography,
	Marker,
	Annotation,
} from "react-simple-maps";

import { makeStyles } from '@material-ui/core/styles';

import EcuadorSvg from '../components/EcuadorSvg';
import GeorgiaSvg from '../components/GeorgiaSvg';

import geoJson from '../resource/geo/world-50m.json';
import geoJsonStates from '../resource/geo/states-10m.json';

const useStyles = makeStyles((theme) => ({
	mapControls: {
		display: 'flex',
		position: 'fixed',
		bottom: '80px',
		left: '50%',
		margin: '1.25rem 0',
		textAlign: 'center',
		transform: 'translateX(-50%)',
	},
	zoomButton: {
		display: 'flex',
		justifyContent: 'center',
		alignItems: 'center',
		padding: 0,
		margin: '0.5em',
		height: '2rem',
		width: '2rem',
		background: '#35220d',
		color: '#fff',
		borderRadius: '25%',
		border: 0,
		'&:focus': {
			outline: 'none',
		}
	},
	markerLabel: {
		fontFamily: "'Josefin Slab', serif",
		fontWeight: 600,
		fontSize: '3px',
		color: "#eee",
		backgroundColor: "#8b602b",
		whiteSpace: 'nowrap',
		padding: "0 1px",
		cursor: "pointer",
		boxShadow: "none",
	}
}));

// const geoUrl = "https://raw.githubusercontent.com/zcreativelabs/react-simple-maps/master/topojson-maps/world-50m.json";

type Data = {
  site: {
    siteMetadata: {
      title: string
    }
  }
  allMarkdownRemark: {
    edges: {
      node: {
        excerpt: string
        frontmatter: {
		  name: string
		  coordinates: Array<number>
		  markerOffset: Array<number>
        }
        fields: {
          slug: string
        }
      }
    }[]
  }
}

const MapLayout = ({ location, title, children }) => {

	return (
		<Layout location={location} title={title}>
			<div
				style={{
					background: `#47261c`,
					height: "100vh",
				}}
				className="mapLayout"
			>
				<main
					style={{
						padding: `0`,
						height: "100%",
					}}
				>
					{children}
				</main>
			</div>
		</Layout>
	)
}

const Map = ({ data, location }: PageProps<Data>) => {
	const siteTitle = data.site.siteMetadata.title
	const posts = data.allMarkdownRemark.edges

	const classes = useStyles();

	const [position, setPosition] = useState({
		coordinates: [282, -3],
		zoom: 7
	});

	function handleZoomIn() {
		if (position.zoom >= 32) return;
		setPosition({ ...position, zoom: position.zoom + 2 });
	}

	function handleZoomOut() {
		if (position.zoom <= 2) return;
		setPosition({ ...position, zoom: position.zoom - 2 });
	}

	function handleMoveEnd(position) {
		setPosition(position);
	}

	return (
		<MapLayout location={location} title={siteTitle}>
			<SEO title="Map" />
			<div style={{display: "flex", flexDirection: "column", height: '100%', overflowY: "auto"}}>
				<ComposableMap
					style={{flexGrow: 1}}
					projection="geoAzimuthalEqualArea"
					projectionConfig={{
						rotate: [80, 4, 0],
						scale: 1000
					}}
				>
					<ZoomableGroup zoom={position.zoom} maxZoom={32} center={position.coordinates} onMoveEnd={handleMoveEnd}>
						<Geographies geography={geoJson}>
							{({ geographies }) =>
							geographies
							.filter(d => d.properties.REGION_UN === "Americas")
							.map(geo => (
								<Geography
									key={geo.rsmKey}
									geography={geo}
									fill={geo.properties.ISO_A3 === "ECU" ? "none" : "#dcd2c1"}
									stroke="#47261c"
									strokeWidth="0.5"
								/>
							))
							}
						</Geographies>
						<Marker coordinates={[-81.05083056, 1.421069444]}>
							<EcuadorSvg />
						</Marker>
						<Marker coordinates={[-103.15083056, 13.281069444]}>
							<GeorgiaSvg fill="#42201e" />
						</Marker>
						<Marker coordinates={[-103.40083056, 13.421069444]}>
							<GeorgiaSvg fill="rgb(255, 250, 242)" />
						</Marker>
						
						{posts.map(({ node }, index) => {
							const name = node.frontmatter.name;
							const coordinates = node.frontmatter.coordinates;
							const markerOffset = node.frontmatter.markerOffset;

							return (
								<Annotation
									subject={coordinates}
									dx={markerOffset[0]}
									dy={markerOffset[1]}
									connectorProps={{
										stroke: "#8b602b",
										strokeWidth: 0.25,
										strokeLinecap: "round"
									}}
									key={index}
								>
									<foreignObject width={1} height={1} style={{overflow: 'visible'}} x={-12} y={-15}>
										<Link className={classes.markerLabel} to={`/farmer/${node.fields.slug}`}>
											{name}
										</Link>
									</foreignObject>
								</Annotation>
							)
						})}
					</ZoomableGroup>
				</ComposableMap>
			</div>
		</MapLayout>
	)
}

export default Map;

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(
		sort: { fields: [frontmatter___name], order: DESC }
		filter: {fileAbsolutePath: {regex: "/farmers/"}}
	) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            name
			tags
			coordinates
			location
			coordinateString
			altitude
			process
			flavor
			otherCrops
			markerOffset
          }
        }
      }
    }
  }
`
